
import { Options, Vue } from "vue-class-component";
import { Plotter } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class LoggerDisplay extends Vue {
  plotter!: Plotter;

  xMax = 600;
  yMax = 320;

  x = 0.0;
  y = 0.0;
  rate = 10.0;

  get canSend(): boolean {
    return (
      this.x >= 0 &&
      this.x <= this.xMax &&
      this.y >= 0 &&
      this.y <= this.yMax &&
      this.rate > 0 &&
      this.rate < 65535
    );
  }

  async sendXY(): Promise<void> {
    await this.plotter.sendXY(this.x, this.y, this.rate);
  }
}
